*{
  padding: 0%;
  margin: 0%;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;

}
.body{
  display: flex;
  justify-content: center;
  height: 100vh;
   
  align-items:flex-start;
}
.main-container{
  border:1px solid rgb(223, 223, 223);
  width:350px;
  min-height: 500px;
  padding:0 20px;
  padding-bottom: 20px;
  
  display:flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
  box-shadow: 1px 1px 15px  rgba(128, 128, 128, 0.308);
  margin-top: 100px;
}
.title{
  margin:10px ;
  font-weight: 300;
}
.input-box-container{
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
}
.input-box{
  width: 75%;
  outline: none;
  height: 30px;
  border-radius: 5px;
  border: 1px solid rgb(177, 177, 177);
  padding:0 10px;

}
.input-box:focus{
  outline: none;
  border: 1px solid rgb(214, 214, 214);
  box-shadow: 0px 0px 7.5px rgb(110, 250, 208);
  border-radius: 5px;
}
.input-box-container button{
  width: 20%;
  border-radius: 50px;
  border: none;
  background-color:rgb(13, 238, 170) ;
}
.input-box-container button:hover{
  cursor: pointer; 
  box-shadow: 2px 2px 5px rgb(163, 163, 163);
}
.todo-main-container{
  
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 15px;
  margin:10px;
  border-radius: 10px;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgb(163, 163, 163);
}
.del-button{
  border-radius: 10px;
  border: none;
 
  
}
.del-button:hover{
  background-color: rgb(255, 132, 132) ;
  cursor: pointer;
  border-radius: 10px;
  border: none;
  color: #ffcc8a;

}
.done-button{
  width:40px;
  height: 30px;
  border-radius: 5px;
  background-color: rgb(255, 255, 255);
  border: none;
 
}
.done-button:hover{
  background-color: #b9ff84 ;
  cursor: pointer;
 
}
